 $font-1: "Roboto", sans-serif;
 $font-2: "Roboto", serif;
 $font-3: "Roboto", serif;

 $color-1: #0E1D34;
 $color-2: #130437;
 $color-3: rgb(0, 22, 44);
 $color-4: #fdeeee;
 $black: rgb(227, 212, 212);
 $white: rgb(253, 253, 253);
 $shadow: rgb(219, 218, 218);

 .skeleton {
     .card-container {
         display: grid;
         grid-template-columns: 66px 1fr;
         border: 2px solid $color-2;
         margin: 5px;
         padding: 16px;
         border-radius: 20px;
         margin: 8px auto;
         position: relative;
         height: auto;
         overflow-x: hidden;
         background-color: #313131;
         animation: skeleton 1s ease infinite alternate;

         @keyframes skeleton {
             to {
                 opacity: .5;
             }
         }

         @media screen and (max-width: 590px) {
             grid-template-columns: 1fr;
         }

         @media screen and (max-width: 440px) {
             padding-top: 26px;
         }

         .fa-spinner {
             position: absolute;
             top: 40%;
             left: 49%;
             font-size: 1.5rem;
             color: $color-1;
         }

         .card-left {
             img {
                 height: 52px;
                 width: 52px;
                 border-radius: 20px;
                 box-shadow: 1px 1px 3px rgba(51, 51, 51, 0.192);
                 object-fit: cover;

                 @media screen and (max-width: 590px) {
                     height: 40px;
                     width: 40px;
                     margin-left: 18px;
                     border-radius: 14px;
                 }
             }

             @media screen and (max-width: 590px) {
                 position: absolute;
                 transform: translate(17px, 18px);
             }
         }

         .card-right {
             @media screen and (max-width: 590px) {
                 margin-left: 18px;
             }

             .card-header {
                 display: flex;
                 justify-content: space-between;
                 margin: 2px 0 4px;
                 min-height: 35px;

                 @media screen and (max-width: 590px) {
                     margin: 12px 0 14px;
                 }

                 @media screen and (max-width: 440px) {
                     flex-direction: column;
                 }

                 .pseudo {
                     display: flex;

                     @media screen and (max-width: 590px) {
                         margin-left: 52px;
                     }

                     @media screen and (max-width: 440px) {
                         transform: translateY(-18px);
                     }

                     span {
                         transform: translate(0px, -5px);

                         @media screen and (max-width: 440px) {
                             margin: 0px 0 -10px;
                             padding: 0;
                         }

                         img {
                             padding: 5px;
                             cursor: pointer;
                             transition: 0.15s;
                             box-shadow: none;
                             height: 28px;
                             width: 28px;

                             &:hover {
                                 transform: scale(1.2) translate(0px, 0px);
                             }
                         }

                         .fas {
                             color: $color-1;
                         }

                         .far {
                             color: $color-2;
                         }
                     }
                 }

                 span {
                     font-style: italic;
                     font-family: $font-1;

                     @media screen and (max-width: 440px) {
                         transform: translate(52px, -20px);
                     }
                 }
             }

             p {
                 margin: 3px 0 9px;
                 font-size: 1.1rem;

                 @media screen and (max-width: 440px) {
                     margin-top: -18px;
                 }
             }

             .update-post {
                 textarea {
                     font-size: 1.3rem;
                     border: 1px solid $color-2;
                     padding: 10px;
                     border-radius: 20px 20px 6px 20px;
                 }

                 .button-container {
                     .btn {
                         background: $color-2;
                         width: 240px;
                         transform: translateY(-3px);

                         &:hover {
                             background: $color-1;
                             width: 250px;
                             letter-spacing: 1px;
                         }
                     }
                 }
             }

             .card-pic {
                 width: 100%;
                 // height: 300px;
                 // object-fit: cover;
                 border-radius: 6px;
                 margin-top: 12px;
                 box-shadow: 0 0 3px rgba(51, 51, 51, 0.342);
             }

             iframe {
                 width: 100%;
                 min-height: 330px;
                 border-radius: 6px;
                 box-shadow: 0 0 3px rgba(51, 51, 51, 0.342);
             }

             .button-container {
                 justify-content: flex-end;
                 display: flex;

                 div {
                     padding: 5px 9px;
                     margin-left: 5px;
                     background: $color-2;
                     border-radius: 50px;
                     transition: 0.1s;
                     cursor: pointer;

                     &:hover {
                         transform: scale(1.07);
                     }

                     img {
                         height: 16px;
                         width: 16px;
                         transform: translateY(3px);
                     }
                 }
             }

             .card-footer {
                 display: flex;
                 align-items: center;
                 justify-content: center;
                 justify-content: space-between;
                 padding: 12px 0 4px;

                 img {
                     height: 22px;
                     width: 22px;
                     cursor: pointer;
                     transition: 0.2s;

                     &:hover {
                         transform: scale(1.2);
                     }
                 }

                 .comment-icon,
                 .like-container {
                     display: flex;
                     align-items: center;

                     span {
                         font-size: 1.02rem;
                         position: absolute;
                         transform: translate(29px, 2px);
                     }
                 }
             }

             .comment-container.client {
                 background: $color-4;
                 border: none;
                 border-radius: 20px 20px 6px 20px;
             }

             .comment-container {
                 display: grid;
                 grid-template-columns: 50px 1fr;
                 border: 1px solid $color-2;
                 padding: 15px;
                 margin: 5px 0;
                 border-radius: 20px 20px 20px 6px;

                 img {
                     height: 40px;
                     width: 40px;
                     border-radius: 14px;
                     box-shadow: 1px 1px 3px rgba(51, 51, 51, 0.192);
                     object-fit: cover;

                     @media screen and (max-width: 440px) {
                         height: 37px;
                         width: 37px;
                     }
                 }

                 .comment-header {
                     display: flex;
                     justify-content: space-between;

                     @media screen and (max-width: 440px) {
                         flex-direction: column;
                     }

                     .pseudo {
                         display: flex;

                         h3 {
                             font-family: $font-1;
                         }

                         span {
                             transform: translate(6px, 4px);
                             cursor: pointer;

                             img {
                                 box-shadow: none;
                                 height: 15px;
                                 width: 15px;
                             }
                         }
                     }

                     span {
                         font-size: 0.9rem;
                         font-style: italic;
                         font-family: $font-1;
                     }
                 }

                 p {
                     margin-top: 8px;
                 }

                 .edit-comment {
                     color: $color-2;

                     img {
                         float: right;
                         margin-left: 10px;
                         cursor: pointer;
                         transition: 0.15s;
                         box-shadow: none;
                         height: 20px;
                         width: 20px;

                         &:hover {
                             transform: scale(1.1);
                         }
                     }

                     .edit-comment-form {
                         margin-top: 14px;

                         label {
                             background: $white;
                             color: $color-1;
                             padding: 6px 12px 5px;
                             border-radius: 20px 20px 20px 6px;
                             cursor: pointer;
                             transition: 0.2s;
                             display: inline-block;
                             font-family: $font-3;

                             &:hover {
                                 transform: scale(1.05);
                             }
                         }

                         input[type="text"] {
                             margin: 4px 0;
                             background: $white;
                             font-size: 1.15rem;
                             padding: 16px;
                             width: 100%;
                             border-radius: 6px 20px 6px 20px;
                         }

                         .btn {
                             display: flex;
                             align-items: center;
                             float: right;

                             span {
                                 background: $white;
                                 padding: 10px 5px;
                                 border-radius: 50px;
                                 margin-right: 6px;
                                 transition: 0.2s;
                                 cursor: pointer;

                                 &:hover {
                                     background: $color-2;
                                 }

                                 img {
                                     transform: translate(-5px, 0);
                                 }
                             }

                             input[type="submit"] {
                                 background: $color-2;
                                 width: inherit;
                                 transition: 0.2s;

                                 &:hover {
                                     width: inherit;
                                     background: $color-1;
                                 }
                             }
                         }
                     }
                 }
             }

             .comment-form {
                 margin-top: 6px;

                 input[type="text"] {
                     width: 100%;
                     background: $color-4;
                     border-radius: 20px 20px 6px 20px;
                     padding: 20px;
                     font-size: 1.4rem;

                     &:focus {
                         box-shadow: 0 0 2px rgba(51, 51, 51, 0.5);
                     }
                 }

                 input[type="submit"] {
                     margin-top: 4px;
                     padding: 12px 16px;
                     width: 120px;
                     background: $color-2;
                     border-radius: 20px 6px 20px 20px;
                     transition: 0.2s;
                     float: right;

                     &:hover {
                         background: $color-1;
                         letter-spacing: 2px;
                         width: 130px;
                     }
                 }

                 button {
                     float: right;
                     background: $color-1;
                     transition: 0.2s;

                     &:hover {
                         background: $color-2;
                     }
                 }
             }

             .like-container {
                 span {
                     font-size: 1.02rem;
                     position: absolute;
                     transform: translate(8px, -1px);
                 }
             }

             .fa-share-alt {
                 transform: translate(0, 2px);
             }
         }

         .update-post {
             textarea {
                 width: 100% !important;
                 height: 80px;
             }

             .button-container {
                 text-align: right;

                 button {
                     margin: 0;
                     margin-left: 5px;
                     width: 150px;

                     &:nth-child(1) {
                         background: $color-1;
                     }
                 }
             }
         }
     }
 }